.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* custom css's */

.blackbackgroung {
  background: black;
  color: white;
  width: 100%;
  height: 100%;
  position: absolute;
  margin: auto;
}

.logo {
  width: 150px;
  height: 150px;
}

.logodiv {
  position: absolute;
  bottom: 50%;
  left: 0;
  right: 0;
}

input {
  outline: none;
  background: black;
  border: black;
  color: wheat;
}

.inputdiv {
  position: absolute;
  bottom: 35%;
  left: 0;
  right: 0;
}

button {
  margin-top: 5%;
  width: 30%;
  border-radius: 25px;
  background: black;
  color: white;
}

.imagetab {
  background-color: #333;
  overflow: auto;
  white-space: nowrap;
}

.horizontalScroll {
  background-color: black;
  overflow: auto;
  white-space: nowrap;
  margin: 20px;
  position: absolute;
  width: 90%;
  bottom: 10px;
}

::-webkit-scrollbar {
  width: 0px;
  background: transparent;
  /* make scrollbar transparent */
}

img {
  width: 80px;
  border-radius: 50px;
  height: 90px;
  padding: 0 10px;
}

.backToScreen {
  width: 80px;
  height: 30px;
  transform: rotate(180deg);
  position: absolute;
  left: 0;
  top: 15px;
  z-index: 9;
}

.backImageDiv {
  height: 10vh;
  margin-top: 50px;
}

.charImage {
  width: 150px;
  height: 150px;
  border-radius: 80px;
}

.Heading {
  position: absolute;
  top: 10%;
  left: 0;
  right: 0;
}

h2 {
  position: absolute;
  left: 0;
  right: 0;
}

table {
  width: 95%;
}

td {
  width: 50%;
}

tr {
  height: 30px;
}

.searchimg {
  width: 50px;
  height: 30px;
}